import React, { Component } from 'react';
import Select, {Option} from '@material/react-select';
import {Switch} from '@rmwc/switch';
import {Button} from '@rmwc/button';
import '@material/react-list/dist/list.css';
import '@material/react-menu-surface/dist/menu-surface.css';
import '@material/react-menu/dist/menu.css';
import '@material/react-select/dist/select.css';
import '@material/react-layout-grid/dist/layout-grid.css';
import '@rmwc/switch/styles';
import './style.css'
import PublishIcon from '@material-ui/icons/Publish';
import SendIcon from '@material-ui/icons/Send';
import axios from 'axios';
import TextResult from './textResult';
import {LinearProgress} from '@rmwc/linear-progress';
import languagesData from './languages.json';
import ProgressBar from 'react-bootstrap/ProgressBar'
import RefreshIcon from '@material-ui/icons/Refresh';
import { Line } from 'rc-progress';

class Form extends Component {
    constructor(props) {
        super(props);
        this.state = this.initialState
        
        
        this.handleOnClick = this.handleOnClick.bind(this);
        this.onUpload=this.onUpload.bind(this)

    }
    componentDidMount(){
        this.formOptions();

    };
    formOptions=()=>{
        const options=[];
        for(var i = 0; i < languagesData.length; i++) {
            var obj = {};
            // console.log(languagesData[i][1].content)                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                     
            obj['value'] = languagesData[i][1].content;                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                 
            obj['label'] = languagesData[i][0].content;
            options.push(obj);
        }
        this.setState({options:options})

    }
    get initialState() {
        return {
            value_lang: "pt-BR",
            value_diarization:"Off",
            puntuation:false,
            selectedFile: null,
            loaded:true,
            result_text:'', 
            show:false,
            isLoading: false,
            options:[],
            speakers:'1',
            disabled:true,
            perc:0
        };
      }
    
    onUpload=progressEvent=>{
        var percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total)
        this.setState({perc:percentCompleted})
        console.log(this.state.perc)
    }
    onClickReset=()=>{
        this.setState(this.initialState)
    }

    onChangeHandler=event=>{
        this.setState({loaded:false})
        this.setState({
          selectedFile: event.target.files[0],
          
        })
        
    }
    
    isDisabled=()=>{
        if (this.state.value_diarization==='On'){
            return false
        }else{
            return true
        }
    }
    handleOnClick=()=>{
        
        const config = {
            onUploadProgress:this.onUpload
            // onUploadProgress: function(progressEvent) {
            //   var percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total)
            //   this.setState({perc:percentCompleted})
            //   console.log(this.state.perc)
            // }
        }

        this.setState({ isLoading: true });
        const data = new FormData()
        data.append('file', this.state.selectedFile)
        data.append('lang',this.state.value_lang )
        data.append('diari',this.state.value_diarization)
        data.append('punt',this.state.puntuation)
        data.append('n_speakers',this.state.speakers)
        console.log('DATA',data.file)
        //'https://speechtotextall.herokuapp.com/speechtotext/'
        // 'http://localhost:3000/speechtotext'
        axios.post('http://138.197.132.198:3000/speechtotext',data,config)
          .then((response)=>{
            console.log('here front',response)
            
            this.setState({result_text:response.data,show:true,isLoading: false})
            
            
            
  
          },(error)=>{
            console.log(error)
          })
  
    }
    
    
    render() { 
        return (
            
            
            <div className="container">
                <div className="container-fluid" style={{marginTop:'5%',paddingBottom:'5%',paddingTop:'5%'}}>
                    
                    
                        <div className="row">
                            <Select
                                label='Language'
                                value={this.state.value_lang}
                                onChange={(evt) => this.setState({value_lang: evt.target.value})}
                                className="container-fluid"
                                
                                        
                            
                            >   
                                {this.state.options.map((val)=><Option value={val.value}>{val.label}</Option>)}
                                
                                
                            </Select>

                        </div>
                        <div className="row align-items-center" style={{marginTop:'5%'}}>
                            <div className="col-sm">
                            
    
                                
                                <Select
                                    label='Speaker diarization'
                                    value={this.state.value_diarization}
                                    onChange={(evt) => this.setState({value_diarization: evt.target.value,disabled:!this.state.disabled})}
                                    className="container-fluid"
                                    color='primary'
                                >   

                                    <Option value='Off'>Off</Option>
                                    <Option value='On'>On</Option>
                                </Select>
                            
                                                                                                                                                                    
                            </div>
                            <div className="col-sm">                                
                                <Select 
                                    disabled={this.state.disabled}
                                    label='Speakers'
                                    value={this.state.speakers}
                                    onChange={(evt) => this.setState({speakers: evt.target.value})}
                                    className="container-fluid"
                                    color='primary'
                                >   

                                    <Option value='1'>1 Speaker</Option>
                                    <Option value='2'>2 Speakers</Option>
                                    <Option value='3'>3 Speakers</Option>
                                    <Option value='4'>4 Speakers</Option>
                                    <Option value='5'>5 Speakers</Option>
                                    
                                </Select>
                            
                                                                                                                                                                    
                            </div>
                            <div className="col-sm h-100 d-flex justify-content-center">
                                <Switch
                                    checked={this.state.puntuation}
                                    onChange={evt => this.setState({puntuation:!!evt.currentTarget.checked})}
                                    label="Puntuation"
                                />
                            </div>
                            

                        </div>
                        <div className="row" style={{marginTop:'5%'}} >
                            
                            <div className="col-sm">                          
                            

                                <div class="upload-btn-wrapper">
                                
                                    <Button raised variant='contained' color='primary'><PublishIcon></PublishIcon> Choose File </Button>
                                    <input  type="file" name="audio" onChange={this.onChangeHandler}/>
                                
                                </div>

                            </div>
                            <div className="col-sm">
                                
                                
                                <Button raised disabled={ this.state.loaded || this.state.isLoading} onClick={this.handleOnClick}><SendIcon></SendIcon> To Text</Button>
                                
                                

                            </div>
                            
                        
                        </div>
                        
                        
                        
                                
                        
                        
                    
                </div>
                
                {this.state.isLoading && <ProgressBar now={this.state.perc} label={this.state.perc} />}
                { this.state.show && <React.Fragment><TextResult text={this.state.result_text} /> <Button onClick={this.onClickReset} icon='refresh' ></Button></React.Fragment>}

                 
            </div>
            

        );
    }
}
 
export default Form;