import React, { Component } from 'react';
import { Typography } from '@material-ui/core';
class TextResult extends Component {
    constructor(props) {
        super(props);
          this.state = {
            text: this.props.text,
            
        }
        
       
    }
    
    render() { 
        return (
            <div className="container-fluid h-100 d-flex justify-content-center" style={{backgroundColor:"#f1f3f4",marginTop:'5%',paddingBottom:'5%',paddingTop:'5%'}}>
                <Typography variant="subtitle1" component="subtitle1" align="justify" paragraph="true" display="block">  
                    {this.state.text}
                </Typography>
                
            </div>

        );
    }
}
 
export default TextResult;